/**
 * Featured Video Component
 *
 * @see https://github.com/WordPress/gutenberg/blob/master/packages/editor/src/components/post-featured-image/index.js
 */

const { wp } = window;
const { __ } = wp.i18n;
const {
	Button, Spinner, ResponsiveWrapper,
} = wp.components;
const { compose } = wp.compose;
const { withSelect, withDispatch } = wp.data;
const { getEditedPostAttribute } = wp.data.select('core/editor');
const { MediaUpload, MediaUploadCheck, PostFeaturedImageCheck } = wp.editor;

// Limit the MediaUpload component to videos only.
const ALLOWED_MEDIA_TYPES = ['video'];

// Labels
const DEFAULT_FEATURE_VIDEO_LABEL = __('Featured Video');
const DEFAULT_SET_FEATURE_VIDEO_LABEL = __('Set featured video');
const DEFAULT_REMOVE_FEATURE_VIDEO_LABEL = __('Remove featured video');

function PostFeaturedVideo({
	featuredVideoId, onUpdateVideo, onRemoveVideo, media,
}) {
	const instructions = <p>{ __('To edit the featured video, you need permission to upload media.') }</p>;

	let mediaSourceUrl;
	if (media) {
		mediaSourceUrl = media.source_url;
	}

	return (
		<PostFeaturedImageCheck>
			<div className="editor-post-featured-image editor-post-featured-video">
				{ !!featuredVideoId
					&& (
						<MediaUploadCheck fallback={instructions}>
							<MediaUpload
								title={DEFAULT_FEATURE_VIDEO_LABEL}
								onSelect={onUpdateVideo}
								allowedTypes={ALLOWED_MEDIA_TYPES}
								modalClass="editor-post-featured-image__media-modal"
								render={({ open }) => (
									<Button className="editor-post-featured-image__preview" onClick={open} aria-label={__('Edit or update the video')}>
										{ media
										&& (
											<ResponsiveWrapper
												naturalWidth={320}
												naturalHeight={180}
											>
												<video width="320" height="180" controls>
													<source src={mediaSourceUrl} type="video/mp4" />
												</video>
											</ResponsiveWrapper>
										)}
										{ !media && <Spinner /> }
									</Button>
								)}
								value={featuredVideoId}
							/>
						</MediaUploadCheck>
					)}
				{ !!featuredVideoId && media && !media.isLoading
					&& (
						<MediaUploadCheck>
							<MediaUpload
								title={DEFAULT_FEATURE_VIDEO_LABEL}
								onSelect={onUpdateVideo}
								allowedTypes={ALLOWED_MEDIA_TYPES}
								modalClass="editor-post-featured-image__media-modal"
								render={({ open }) => (
									<Button onClick={open} isDefault isLarge>
										{ __('Replace video') }
									</Button>
								)}
							/>
						</MediaUploadCheck>
					)}
				{ !featuredVideoId
					&& (
						<div>
							<MediaUploadCheck fallback={instructions}>
								<MediaUpload
									title={DEFAULT_FEATURE_VIDEO_LABEL}
									onSelect={onUpdateVideo}
									allowedTypes={ALLOWED_MEDIA_TYPES}
									modalClass="editor-post-featured-image__media-modal"
									render={({ open }) => (
										<Button className="editor-post-featured-image__toggle" onClick={open}>
											{DEFAULT_SET_FEATURE_VIDEO_LABEL}
										</Button>
									)}
								/>
							</MediaUploadCheck>
						</div>
					)}
				{ !!featuredVideoId
					&& (
						<MediaUploadCheck>
							<Button onClick={onRemoveVideo} isLink isDestructive>
								{DEFAULT_REMOVE_FEATURE_VIDEO_LABEL}
							</Button>
						</MediaUploadCheck>
					)}
			</div>
		</PostFeaturedImageCheck>
	);
}

/**
 * Inject state-derived props into PostFeaturedVideo
 *
 * @see https://wordpress.org/gutenberg/handbook/packages/packages-data/#withselect-mapselecttoprops-function-function
 * @see https://wordpress.org/gutenberg/handbook/data/data-core-editor/#geteditedpostattribute
 */
const applyWithSelect = withSelect((select) => {
	const { getMedia } = select('core');
	const meta = getEditedPostAttribute('meta');
	const featuredVideoId = meta ? meta.em_featured_video_id : false;

	/**
	 * Return data for use inside PostFeaturedVideo
	 */
	return {
		media: featuredVideoId ? getMedia(featuredVideoId) : null,
		featuredVideoId,
	};
});

/**
 * Inject dispatching action props into PostFeaturedVideo
 *
 * @see https://wordpress.org/gutenberg/handbook/packages/packages-data/#withdispatch-mapdispatchtoprops-function-function
 * @see https://wordpress.org/gutenberg/handbook/data/data-core-editor/#editpost
 * @see https://wordpress.org/gutenberg/handbook/data/data-core-editor/#geteditedpostattribute
 */
const applyWithDispatch = withDispatch((dispatch) => {
	const { editPost } = dispatch('core/editor');
	const meta = getEditedPostAttribute('meta');

	/**
	 * Helper function that updates em_featured_video_id,
	 * while preserving existing meta.
	 */
	const updateFeaturedVideoId = (videoId) => {
		editPost({
			meta: {
				...meta,
				em_featured_video_id: videoId,
			},
		});
	};

	/**
	 * Return event handlers used within PostFeaturedVideo
	 */
	return {
		onUpdateVideo(video) {
			updateFeaturedVideoId(video.id);
		},
		onRemoveVideo() {
			updateFeaturedVideoId(0);
		},
	};
});

/**
 * Export PostFeaturedVideo component by merging the returned
 * values from applyWithSelect and applyWithDispatch and passing
 * them into the component to create a Higher Order Component.
 *
 * @see https://wordpress.org/gutenberg/handbook/packages/packages-compose/
 */
export default compose(
	applyWithSelect,
	applyWithDispatch,
)(PostFeaturedVideo);
