/**
 * Hook into editor.PostFeaturedImage in order to append our
 * PostFeaturedImage component onto PostFeaturedImage.
 *
 * @see https://github.com/WordPress/gutenberg/tree/master/packages/editor/src/components/post-featured-image
 */

import PostFeaturedVideo from './post-featured-video';

const { wp } = window;
const { __ } = wp.i18n;
const { domReady } = wp;
const { Fragment } = wp.element;
const { addFilter } = wp.hooks;

/**
 * Check if the current post supports featured video or not by checking
 * the body class for the 'post-type-{type}` class.
 */
function postTypeSupportsFeaturedVideo() {
	// Update array with all post types that should support featured videos.
	const supportedPostTypes = ['page'];
	// If the current post's post type is supported, return true.
	return supportedPostTypes.some((postType) => document.body.classList.contains(`post-type-${postType}`));
}

/**
 * Append to the WordPress PostFeaturedImage component,
 * adding our PostFeaturedVideo component.
 *
 * @param {PostFeaturedImage} OriginalPostFeaturedImage
 */
function addPostFeaturedVideo(OriginalPostFeaturedImage) {
	return function (props) {
		return (
			<Fragment>
				<OriginalPostFeaturedImage {...props} />
				<PostFeaturedVideo />
			</Fragment>
		);
	};
}

domReady(() => {
	// Only add the featured video to pages.
	if (postTypeSupportsFeaturedVideo()) {
		addFilter(
			'editor.PostFeaturedImage',
			'embark/add-post-featured-video',
			addPostFeaturedVideo,
		);

		/**
		 * Replace "Featured Image" text in Document Sidebar with "Featured Media"
		 */
		const editPostSidebar = document.querySelector('.edit-post-sidebar');
		if (editPostSidebar) {
			const changeFeaturedImageLabel = () => {
				const panelToggles = editPostSidebar.querySelectorAll('.components-panel__body-toggle');
				panelToggles.forEach((panelToggle) => {
					if (panelToggle.textContent.toLowerCase() === 'featured image') {
						panelToggle.childNodes.forEach((node) => {
							if (node.nodeType === 3) {
								node.textContent = __('Featured Media');
							}
						});
					}
				});
			};

			// Change the label when the page first loads.
			changeFeaturedImageLabel();

			/**
			 * Then change the label every time the "Document" sidebar becomes active.
			 * Uses MutationObserver to watch for attribute modifications on the sidebar
			 * toggle.
			 *
			 * @see https://developer.mozilla.org/en-US/docs/Web/API/MutationObserver
			 */
			const documentSidebarToggle = document.querySelector('.edit-post-sidebar__panel-tab[data-label="Document"]');
			if (documentSidebarToggle) {
				// Create an observer instance linked to the callback function
				const observer = new MutationObserver(changeFeaturedImageLabel);
				// Start observing the target node for configured mutations
				observer.observe(documentSidebarToggle, { attributes: true });
			}
		}
	}
});
