/**
 * Unregister block type
 *
 * @package Embark
 * @since   1.0.0
 */
// eslint-disable-next-line
const { unregisterBlockType, getBlockTypes } = wp.blocks;

/**
 * We have to wait until all blocks have been registered before unregistering them.
 *
 * This is SUPER fragile and hacky and will likely cause things to break over time, so I suggest
 * keeping an eye on Gutenberg to see how blacklisting/whitelisting evolves in the core plugin.
 */
function initBlacklist() {
	const blacklistBlocks = [
		'core/inline-image',
		'core/verse',
		'core/audio',
		'core/code',
		'core/preformatted',
		'core/text-columns',
		'core-embed/wordpress',
		'core-embed/soundcloud',
		'core-embed/spotify',
		'core-embed/flickr',
		'core-embed/animoto',
		'core-embed/cloudup',
		'core-embed/collegehumor',
		'core-embed/dailymotion',
		'core-embed/funnyordie',
		'core-embed/hulu',
		'core-embed/imgur',
		'core-embed/issuu',
		'core-embed/kickstarter',
		'core-embed/meetup-com',
		'core-embed/mixcloud',
		'core-embed/photobucket',
		'core-embed/polldaddy',
		'core-embed/reddit',
		'core-embed/reverbnation',
		'core-embed/screencast',
		'core-embed/scribd',
		'core-embed/smugmug',
		'core-embed/speaker',
		'core-embed/speaker-deck',
		'core-embed/ted',
		'core-embed/tumblr',
		'core-embed/videopress',
		'core-embed/wordpress-tv',
	];

	/**
	 * Loop through all the blockTypes and unregister blacklisted blocks..
	 *
	 */
	if (
		typeof unregisterBlockType !== 'undefined'
		&& getBlockTypes !== 'undefined'
	) {
		getBlockTypes().forEach((block) => {
			if (blacklistBlocks.includes(block.name) === true) {
				unregisterBlockType(block.name);
			}
		});
	}
}

window.addEventListener('DOMContentLoaded', initBlacklist);
