/**
 * Hook into blocks and add default classes.
 *
 * @see https://developer.wordpress.org/block-editor/developers/filters/block-filters/#blocks-getblockdefaultclassname
 */

// get hooks.
const { addFilter } = wp.hooks; // eslint-disable-line

// add Class to lists
addFilter(
	'blocks.getSaveContent.extraProps',
	'embark/set-list-block-class-names',
	(props, blockType) => {
		if (blockType.name === 'core/list') {
			const classes = props.className ? `wp-block-list ${props.className}` : 'wp-block-list';
			return Object.assign(props, { className: classes });
		}
		return props;
	},
);
